/* Question.css */
.question-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    max-width: 600px;
    margin: auto;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.question-image {
    width: 100%;
    max-width: 300px;
    margin-bottom: 20px;
    border-radius: 5px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.answer-buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
}

.answer-button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: 2px solid #4b6584;
    border-radius: 5px;
    background-color: #f0f0f0;
    transition: all 0.2s ease-in-out;
}

.answer-button:hover {
    background-color: #4b6584;
    color: white;
    transform: scale(1.05);
}

.answer-button:active {
    background-color: #2d4059;
}
